<template>
  <div id="deviceName" :style="{height:fullHeight + 'px'}">
    <head-top title="Device Rename" goBack="true"></head-top>
    <div class="content">
      <span>Please name your device</span>
      <div class="editorName" :class="borderColor? 'active_color':''">
        <input type="text" placeholder="e.g.Tom 's plug, Plug in garden "
        v-model="value"
        maxlength="16"
        @focus="borderColor = !borderColor"
        @blur="borderColor = !borderColor"
        @input="nameExp(value)"/>
      </div>
      <span :style="{color:toastFalg? 'red':''}">Tip：1.2-16  characters，only chinese and engish and numbers allowed</span>
      <span>2.Location naming is recommended</span>
      <p>Choose from list of presets for fast set up. Our Presets come preloaded with standard controls speciﬁc for that device use. These can be amended on the device set up controls in ‘Control Centre’.</p>
      <!-- <div class="select" ref="wheelWrapper">
        <ul class="wheel-scroll" >
          <li class="wheel-item" v-for="item in pickData[0]" :key="item.id">{{item}}</li>
        </ul>
      </div> -->
      <picker :data="pickData" class="picker"
              :selected-index="selectedIndex"
              :unit="unit"
              @change="change"
              ref="picker">
      </picker>
      <div class="nextbtn" :class="{nextDisable: !clickOut}" @click="next">
        <span>Next</span>
      </div>
    </div>
  </div>
</template>
<script>
import headTop from '../../../components/common/head'
import picker from '../../../components/common/picker'
import { mapGetters } from 'vuex'
import {reNameRequest} from '../../../api/method'
// import Bscroll from 'better-scroll'
const selectdata = ['Hearter', 'HeatMat', 'Humidifier', 'Light12/12', 'Light15/8', 'AirPump', 'Irrigationpump']
export default {
  data () {
    return {
      borderColor: false, // input 获取焦点时border颜色
      value: this.$route.params.name, // 输入内容
      toastFalg: false, // 输入格式错误提示
      clickOut: true, // 默认不可点击
      // selectdata:,
      pickData: [selectdata],
      unit: [''],
      wheels: null,
      selectedIndex: [2],
      fullHeight: null, // 屏幕高度
      selectedValue: []
    }
  },
  components: {
    headTop,
    picker
  },
  computed: {
    ...mapGetters(['getcurrentMac']),
    currentDeviceMAC () {
      return this.getcurrentMac
    }

  },
  mounted () {
    this.fullHeight = document.documentElement.clientHeight
    let picker = this.$refs.picker
    this.selectedValue = picker.getInitValue()
    picker.show()
    this.value = this.selectedValue
    console.log(this.selectedValue)
  },
  methods: {
    next () {
      if (this.clickOut && !this.toastFalg) {
        console.log(this.value.toString())
        reNameRequest(this.NAVIGATOR, this, this.currentDeviceMAC, this.value.toString())
        this.$store.state.user.deviceName = this.value.toString()
        console.log(this.$store.state.user.deviceName)
        if (this.$root.ISDEBUG) {
          window.reNameResponse(this.currentDeviceMAC, true)
        }
        this.$router.push({path: '/Me/adsuccess'})
      }
    },
    nameExp (val) {
      // 输入内容格式验证
      if (!/^\w{0,}$/.test(val)) {
        this.toastFalg = true
        this.clickOut = false
      } else {
        this.toastFalg = false
        this.clickOut = true
      }
    },
    change (value) {
      this.selectedValue = value
    }
  },
  watch: {
    selectedValue (newval) {
      this.value = newval
      console.log(this.selectedValue)
    }
  }
}
</script>
<style lang="less" scoped>
#deviceName{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  .editorName {
    padding: 0 30px;
    .wh-l(100%, 100px);
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    display: flex;
    justify-content: space-between;
    input {
      flex: 1;
      font-size: 24px;
      background-color: transparent;
      .wh-l(auto, 100%);
      color: black;
      font-weight: 600px;
    }
    input::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #cccccc;
    }
    img {
      .wh(24px, 24px);
      align-self: center;
    }
  }
  .content{
    display: flex;
    flex-direction: column;
    span{
      padding: 0 30px;
      font-size: 25px;
      line-height: 40px;
      text-align: left;
    }
    span:nth-child(1) {
      font-size: 32px;
      line-height: 92px;
      text-align: left;
      .wh(100%, 92px);
    }
    span:nth-of-type(2){
      margin-top: 20px;
      color: #dadada;
    }
    span:nth-of-type(3){
      color: #dadada;
    }
    p{
      font-size: 25px;
      line-height: 35px;
      padding: 0 30px;
      margin-top: 95px;
      text-align: left;
    }
    .select, .picker{
      .wh(100%, 300px);
      padding: 0 30px;
      margin-top: 40px;
      overflow: hidden;
    }
    ul{
      .wh(100%, auto);
      li{
        font-size: 28px;
        color: #cfcfcf;
        .wh-l(100%, 60px)
      }
      li:nth-child(3) {
        color: #5a5a5a;
        font-weight: 900;
      }
    }
  }
  .nextbtn{
      .wh-l(520px,100px);
      margin: 0 auto;
      border-radius: 10px;
      background: #13af68;
      margin-top: 47px;
      span{
        font-size: 47px;
        color: #fff;
        vertical-align: middle;
      }
    }
    .nextDisable{
      background: #D6D6D6;
    }
  .active_color {
    border-color: #01a29a;
  }
}
</style>



